.header {
  margin-bottom: $layout-size_base;
  padding-top: $height_navbar;
  background-image: linear-gradient(to right bottom, $gradient-color_background);

  &__container {
    height: 400px;

    @extend %flex-row-center;
    @extend %layout_container;
  }

  &__content {
    flex: 1;
  }

  &__title {
    margin-bottom: 40px;
    color: $color_white;
  }

  &__illustration {
    flex: 1;

    svg {
      max-height: 360px;
      width: 100%;
    }
  }
}

.header-screen {
  height: $height_navbar;
  background-color: $color_accent;
}
