.section {
  position: relative;
  padding-bottom: 40px;

  &__container {
    @extend %layout_container;

    &--row {
      overflow: hidden;

      @extend %flex-wrap;

      >section {
        flex: 1;
        margin: $row-size_col-margin $row-size_col-margin*2;

        @for $i from 1 through 6 {
          &.flex-#{$i} {
            flex: #{$i};
          }
        }
      }
    }

    &--full {
      max-width: none;
      width: 100%;

      img {
        width: 100%;
      }
    }
  }

  &__title {
    position: sticky;
    top: 0;
    display: block;
    margin-top: 1rem;
    padding: $layout-size_small;
    background-color: $color_accent;
    color: $color_white;
    text-align: center;
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 1.25rem;
    z-index: 10;

    &--center {
      text-align: center;
    }

    &--white {
      color: $color_white;
    }
  }

  &__website {
    width: 100%;
    height: calc(100vh - 80px);
  }
}
