.line-btn {
  display: inline-block;
  margin-right: 10px;
  padding: $row-size_col-margin*1.5 $row-size_col-margin*3;
  border: $color_white solid 1px;
  border-radius: $border-radius_btn*2;
  color: $color_white;
  font-weight: 400;
  cursor: pointer;
  transition: 300ms;

  &--glassmorphism {
    background-color: $color_glassmorphism;
  }

  &:hover {
    transform: translateY(-5px);
  }
}
