@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400;500&family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@400;600&display=swap');

body {
  position: relative;
  background-color: $color_primary;
  color: $color_text-color;
  letter-spacing: 1px;
  font-weight: 300;
  font-family: 'Noto Sans TC', sans-serif;
  line-height: 1.35;
}

$title-size:("h1":3.75rem, "h2":3.25rem, "h3":2.75rem, "h4":2.15rem, "h5":1.65rem, "h6":1.15rem);

@each $tag,
$size in $title-size {
  #{$tag} {
    color: $color_accent;
    letter-spacing: 3px;
    font-weight: 500;
    font-size: $size;
    line-height: 1.35;
  }
}

.text-white {
  color: $color_white;
}

.text-bold {
  font-weight: 500;
}

.text-height {
  letter-spacing: 2px;
  line-height: 1.5;
}

.margin-bottom {
  margin-bottom: $layout-size_small;

  &--big {
    margin-bottom: $layout-size_base;
  }

  &--small {
    margin-bottom: $layout-size_small/2;
  }
}

.margin-top {
  margin-top: $layout-size_small;

  &--big {
    margin-top: $layout-size_base;
  }

  &--small {
    margin-top: $layout-size_small/2;
  }
}

.absolute--left-bottom {
  position: absolute;
  bottom: 0;
  left: 10%;
}
