.website-demo {
  flex-direction: column;

  @extend %layout_list;

  img {
    width: 100%;
  }

  li {
    position: relative;
    overflow: hidden;
    flex: 1;
    min-width: 220px;
    background-color: $color_white;
  }

  &__view {
    position: absolute;
    top: 0;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: $color_black;
    opacity: 0;
    cursor: pointer;
    transition: 300ms;

    @extend %flex-center;

    svg {
      width: 40px;
      height: 40px;

      fill: $color_white;
    }

    li:hover & {
      opacity: 0.7;
    }
  }

  &__title {
    margin-bottom: 20px;
    color: $color_white;
  }
}
