.nav {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: $height_navbar;
  color: $color_white;
  text-transform: uppercase;
  font-weight: 500;

  @extend %flex-row-center;

  span {
    cursor: pointer;
  }

  &__container {
    @extend %flex-row-between;
    @extend %layout_container;
  }

  &__link {
    display: flex;

    span {
      margin-left: $row-size_col-margin;
      color: $color_white;
    }
  }
}
